import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/CardGame/1.png"
import image2 from "../../static/CardGame/2.png"
import image3 from "../../static/CardGame/3.png"
import image4 from "../../static/CardGame/4.png"
import image5 from "../../static/CardGame/5.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Card Game</h1>
        
        <img class="crdgm" src={image1} alt="image1"/>
        <img class="crdgm" src={image2} alt="image2"/>
        <img class="crdgm" src={image3} alt="image3"/>
        <img class="crdgm" src={image4} alt="image4"/>
        <img class="crdgm" src={image5} alt="image5"/>

        <div>
          <p>
          This project started as freelance work but at the moment it's on stand-by. I didn't give it's name but I can show you a bit of how it looks at the moment. 
          It is a collectible card game, meant to be played online, with RPG elements and heavy emphasis on your hero card, the cards in your deck meant to support them.
          <br></br>
          The map is randomly generated, each hex providing different benefits and affecting each class differently.
          <br></br>
          The game is made in Unity using C# and I have used Mirror Networking for the multiplayer element. There are also a couple of shaders made by me using HLSL.
          <br></br>
          The server was deployed on AWS and the client as well. There is a matchmaking system in place and multiple games of two players can be held at the same time.
          <br></br>
          Youtube Link: <a href="https://www.youtube.com/watch?v=4rHvtUKz2jk"> Card Game Trailer </a>
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage